import React, { Component, Fragment } from 'react';

//Libs
import { Switch, Route } from 'react-router-dom';
import Head from './comoponents/Head';
import { withRouter } from 'react-router-dom';
import ObjetivoProfecional from './pages/ObjetivoProfecional';


class ErrorBoundary extends Component {

    state = { error: null, errorInfo: null };

    componentDidCatch = (error, errorInfo) => {
        this.setState({ error, errorInfo })
    }

    render() {
        if (this.state.errorInfo) {
            // Error path
            return (
                <div>
                    <h2>Error 500</h2>
                    <details style={{ whiteSpace: 'pre-wrap' }}>
                        {this.state.error && this.state.error.toString()}
                        <br />
                        {this.state.errorInfo.componentStack}
                    </details>
                </div>
            )
        }
        return this.props.children;
    }
}


const Formularios = () => {
    return (
        <Fragment>
            <Head/>
            formulario
        </Fragment>
    )
}

const Documentos = () => {
    return (
        <Fragment>
            <Head/>
            Documentos
        </Fragment>
    )
}

const NotFound = () => {
    return (<p>no encontrada</p>)
}



class App extends Component {
    render() {
        return (
            <ErrorBoundary>
                <Switch>
                    <Route exact path='/' component={ObjetivoProfecional} />
                    <Route  path='/Formularios' component={Formularios}/>
                    <Route  path='/Documentos' component={Documentos}/>
                    <Route component={ NotFound }/>
                </Switch>
            </ErrorBoundary>
        )
    }
}

export default withRouter(App)